/* eslint-disable prefer-const */
/* eslint-disable func-names */
/* eslint-disable no-shadow */
let isMouseDown;
const framesView = document.getElementById('framesView');
// const colors = document.getElementsByClassName('colors');
const canv = document.getElementById('canvas');
const ctx = canv.getContext('2d');
const canvasWidth = 600;
const canvasHeight = 600;
let pixelCount = 32;
let dotX = [];
let dotY = [];
let dotColor = [];
let coordsY;
let coordsX;
const frames = [];
let primaryColor = 'black';
let secondaryColor = 'white';
let frameNumber = 0;
canv.width = canvasWidth;
canv.height = canvasHeight;
let prevTarget;
let canvSmall = document.getElementById('canvasSmall');
let ctxSmall = canvSmall.getContext('2d');
let canvAnim = document.getElementById('canvas-area');
let ctxAnim = canvAnim.getContext('2d');
let framesCount = 0;

// Проверка нажата ли кнопка.
canv.addEventListener('mousedown', () => { isMouseDown = true; });
canv.addEventListener('mouseup', () => {
  isMouseDown = false;
  canv.onmousemove = null;
});


// Отрисовка canvas на мини фреймах.
function drowMiniFrames() {
  for (let i = 0; i < frames[frameNumber].dotX.length; i += 1) {
    ctxSmall.fillStyle = frames[frameNumber].dotColor[i];
    ctxSmall.fillRect(frames[frameNumber].dotX[i] / (600 / 110), frames[frameNumber].dotY[i] / (600 / 110), 110 / pixelCount, 110 / pixelCount);
  }
}

// Отрисовка на главном фрейме.
function dorwMainFrame() {
  for (let i = 0; i < frames[frameNumber].dotX.length; i += 1) {
    ctx.fillStyle = frames[frameNumber].dotColor[i];
    ctx.fillRect(frames[frameNumber].dotX[i] - canvasWidth / pixelCount, frames[frameNumber].dotY[i] - canvasWidth / pixelCount, canvasWidth / pixelCount, canvasWidth / pixelCount);
  }
}


function createFrameInArray(frameNumber) {
  if (frames[frameNumber] === undefined) {
    frames[frameNumber] = [];
    frames[frameNumber].dotX = [];
    frames[frameNumber].dotY = [];
    frames[frameNumber].dotColor = [];
  }

  frames[frameNumber].dotX.push(dotX);
  frames[frameNumber].dotY.push(dotY);
  frames[frameNumber].dotColor.push(dotColor);
}

// Создание кадров.

function createFrame(e) {
  // Получаем координаты и цвет пикселя.

  for (let i = 1; i < pixelCount + 1; i += 1) {
    if (e.offsetX < ((canvasWidth / pixelCount) * (i))) {
      coordsX = (canvasWidth / pixelCount) * i;
      break;
    } else {
      continue;
    }
  }

  for (let j = 1; j < pixelCount + 1; j += 1) {
    if (e.offsetY < ((canvasWidth / pixelCount) * j)) {
      coordsY = (canvasWidth / pixelCount) * j;
      break;
    } else {
      continue;
    }
  }

  dotColor.push(primaryColor);
  dotX.push(coordsX);
  dotY.push(coordsY);

  // Добавляем полученные пиксели в массив с кадрами и отрисовываем.

  createFrameInArray(frameNumber);
  dotX = [];
  dotY = [];
  dotColor = [];

  ctx.clearRect(0, 0, canv.width, canv.height);
  dorwMainFrame();
  drowMiniFrames();
}

// Отрисовка пикселей по движению мыши.
function pixelDrowOnMouseMove(e) {
  createFrame(e);
  if (isMouseDown) {
    canv.onmousemove = function (e) {
      createFrame(e);
    };
  }
}
// function otherColor() {
//   primaryColor = document.getElementsByClassName('secondary-color')[0].style.background;
// }
// canv.addEventListener('contextmenu', otherColor);
canv.addEventListener('mousedown', pixelDrowOnMouseMove);


function toggleColors(colorList) {
  for (let i = 0; i < document.getElementById('popUp').children.length; i += 1) {
    colorList.children[i].classList.toggle('visible');
  }
}

// Выбор цвета.
function chooseColor(e) {
  const { target } = e;
  const colorList = document.getElementById('popUp');
  if (target.classList.contains('color-picker')) {
    colorList.classList.toggle('color-list');
    toggleColors(colorList);

    colorList.onclick = function (e) {
      if (e.target.classList.contains('color-list-elem')) {
        primaryColor = e.target.style.background;
        colorList.classList.toggle('color-list');
        toggleColors(colorList);
        target.style.background = primaryColor;
      }
    };
  }
}
document.getElementsByClassName('colors')[0].addEventListener('click', chooseColor);

// Выбор количества пикселей.
document.getElementById('setPixelCount').onclick = function () {
  pixelCount = document.getElementById('pixelCount').value;
};


// Выбор кадра.
function chooseFrame(e) {
  let { target } = e;
  if (e.target.classList.contains('canvasSmall')) {
    if (prevTarget) {
      prevTarget.classList.toggle('active');
    }
    ctx.clearRect(0, 0, canv.width, canv.height);
    canvSmall = target;
    ctxSmall = canvSmall.getContext('2d');
    frameNumber = target.getAttribute('data-frameNumber');
    target.classList.toggle('active');
    prevTarget = target;
    dorwMainFrame();
  }
}


framesView.addEventListener('click', chooseFrame);


// Добавить новый кадр.
function addNewFrame() {
  framesCount += 1;
  let frameContainer = document.createElement('div');
  let frame = document.createElement('canvas');
  frameContainer.className = 'frameContainer';
  frame.innerHTML = 'Not working in your browser!';
  frame.className = 'canvasSmall';
  frame.dataset.framenumber = framesCount;
  frame.width = 110;
  frame.height = 110;
  framesView.insertBefore(frameContainer, document.getElementsByClassName('frameContainer')[document.getElementsByClassName('frameContainer').length - 1].nextElementSibling);
  frameContainer.appendChild(frame);

  const delBtn = document.createElement('button');
  delBtn.className = 'btn btn-delete';
  delBtn.innerHTML = 'X';
  frameContainer.appendChild(delBtn);

  const copyImg = document.createElement('img');
  copyImg.src = './src/screens/canvas/images/Без имени-1.png';
  copyImg.className = 'btn-copy img';
  frameContainer.appendChild(copyImg);
}

document.getElementById('addFrame').addEventListener('click', addNewFrame);

// Скопировать кадр

function copyFrame(e) {
  let { target } = e;
  if (target.classList.contains('btn-copy')) {
    addNewFrame();
    let newElemNumber = document.getElementsByClassName('canvasSmall').length - 1;
    createFrameInArray(newElemNumber);
    frames[newElemNumber].dotX = frames[target.previousSibling.previousSibling.getAttribute('data-framenumber')].dotX.slice();
    frames[newElemNumber].dotY = frames[target.previousSibling.previousSibling.getAttribute('data-framenumber')].dotY.slice();
    frames[newElemNumber].dotColor = frames[target.previousSibling.previousSibling.getAttribute('data-framenumber')].dotColor.slice();
    canvSmall = document.getElementsByClassName('canvasSmall')[document.getElementsByClassName('canvasSmall').length - 1];
    ctxSmall = canvSmall.getContext('2d');
    frameNumber = newElemNumber;

    dorwMainFrame();
    drowMiniFrames();
  }
}
framesView.addEventListener('click', copyFrame);

// Удалить кадр

function deleteFrame(e) {
  if (e.target.classList.contains('btn-delete')) {
    e.target.parentElement.parentElement.removeChild(e.target.parentElement);
  }
}

framesView.addEventListener('click', deleteFrame);

// Установка кол-ва кадров в секунду.

let frameRate;
document.getElementById('frameRate').onchange = function () {
  frameRate = document.getElementById('frameRate').value;
};

// Вывод анимации
let interval;
let count = 0;
function startAnim(frameNumber) {

  console.log(frames);
  console.log(frames[+getframeNumbers()[frameNumber]]);
  ctxAnim.clearRect(0, 0, canvAnim.width, canvAnim.height);
  for (let i = 0; i < frames[+getframeNumbers()[frameNumber]].dotX.length; i += 1) {
    ctxAnim.fillStyle = frames[frameNumber].dotColor[i];
    ctxAnim.fillRect(frames[frameNumber].dotX[i] / (600 / 110), frames[frameNumber].dotY[i] / (600 / 110), 110 / pixelCount, 110 / pixelCount);
  }
}

document.getElementById('runAnim').addEventListener('click', () => {
  clearInterval(interval);
  interval = setInterval(() => {
    startAnim(count);
    count += 1;
    if (count === getframeNumbers().length) {
      count = 0;
    }
  }, 1000 / frameRate);
  console.log(frameRate);
});

// Получить номера кадров, которые существуют на данный момент

function getframeNumbers() {
  let frames = document.getElementsByClassName('canvasSmall');
  let framesNumber = [];
  for (let i = 0; i < frames.length; i++) {
    framesNumber.push(frames[i].getAttribute('data-framenumber'));
  }
  return framesNumber;
}
